.w {
  width: 1190px; }

.register-header {
  height: 116px;
  background-color: #fff;
  line-height: 0; }
  .register-header .w {
    height: 56px;
    padding: 30px 0; }
  .register-header .left {
    float: left; }
    .register-header .left .logo {
      position: relative;
      display: inline-block;
      width: 160px;
      top: -10px; }
    .register-header .left span {
      font-size: 24px;
      position: relative;
      top: 28px;
      left: 12px;
      color: black; }
  .register-header .right {
    float: right;
    padding-top: 40px;
    color: black; }

.user-content .w {
  width: 1110px;
  padding: 40px;
  border: 1px solid #d7d7d7; }

.user-content .registerBox {
  position: relative;
  width: 800px;
  border-right: 1px solid #ddd;
  float: left; }
  .user-content .registerBox input {
    width: 300px;
    height: 30px;
    margin: 5px;
    -webkit-border-radius: 5px;
            border-radius: 5px;
    border: 1px solid #ccc;
    padding: 7px 0px;
    -webkit-border-radius: 3px;
            border-radius: 3px;
    padding-left: 5px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s, -webkit-box-shadow ease-in-out .15s; }
  .user-content .registerBox label {
    width: 85px;
    display: inline-block;
    text-align: right; }
  .user-content .registerBox div {
    position: relative;
    margin: 10px 0; }
  .user-content .registerBox input:focus {
    border-color: #66afe9;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6); }
  .user-content .registerBox p {
    display: inline-block;
    width: 300px;
    height: 36px;
    font-size: 12px;
    line-height: 18px;
    color: gray;
    border: 1px solid #eee;
    margin-top: 2px;
    padding: 3px;
    position: absolute;
    left: 405px;
    top: 4px;
    /* display: none; */ }
  .user-content .registerBox .error {
    border: 1px solid #ff5242 !important;
    background-color: #fff3f3;
    color: #d10000; }
  .user-content .registerBox #button {
    width: 307px;
    height: 40px;
    background-color: #cf000e;
    padding: 0;
    outline: 0;
    color: white;
    margin-left: 95px;
    font-size: 14px;
    margin-top: 20px; }
  .user-content .registerBox .iconfont {
    color: green;
    font-size: 20px; }
  .user-content .registerBox i {
    display: none; }
  .user-content .registerBox .telephone_p,
  .user-content .registerBox .confirm_p,
  .user-content .registerBox .code_p {
    line-height: 36px !important;
    display: none; }
  .user-content .registerBox label {
    color: #555; }
    .user-content .registerBox label::before {
      content: '*';
      color: red; }
  .user-content .registerBox .code {
    position: relative; }
    .user-content .registerBox .code #code_num {
      width: 70px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      position: absolute;
      left: 323px;
      top: -2px; }
    .user-content .registerBox .code em {
      display: none;
      color: #e62318 !important; }

.user-content .register-right {
  float: right;
  width: 220px;
  margin-right: 27px; }
  .user-content .register-right .register-ok {
    padding: 20px 0;
    text-align: center;
    line-height: 32px;
    border-bottom: 1px solid #ddd; }
    .user-content .register-right .register-ok .login {
      display: inline-block;
      background: #cf000e;
      border: 0;
      height: 32px;
      width: 70px;
      -webkit-border-radius: 4px;
              border-radius: 4px;
      font-size: 14px;
      color: #fff; }
  .user-content .register-right .img-login {
    padding: 64px 0;
    text-align: center;
    border-bottom: 1px solid #ddd; }
    .user-content .register-right .img-login .wechat {
      width: 120px;
      height: 120px;
      margin: 0 auto; }
    .user-content .register-right .img-login p {
      margin-top: 10px; }
  .user-content .register-right .login-tips p {
    line-height: 28px; }
  .user-content .register-right .login-tips a {
    height: 28px;
    width: 28px;
    display: inline-block;
    margin: 0 5px;
    background: url(/dist/images/allPuzzle.png) no-repeat; }
    .user-content .register-right .login-tips a:nth-of-type(2) {
      background-position: -28px 0; }
    .user-content .register-right .login-tips a:nth-of-type(3) {
      background-position: -86px 0; }

.footer-icon {
  margin-top: 100px; }
  .footer-icon a {
    width: 148px; }
